.main {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 50px;
  }
  
  .strip {
    box-sizing: border-box;
    /* margin-right: 500px;
    margin-top: 50px; */
    /* margin: 50px 500px 50px 200px; */
    padding: 10px;
    box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(255, 255, 255),
      0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
    border-radius: 15px;
    /* height: 48px !important;
    width: 48px !important;
    border-radius: 50% !important; */
  }
  
  .img {
    height: 80px !important;
    width: 80px !important;
    border-radius: 50% !important;
    margin-bottom: 10px;
    margin-right: 30px;
  }
  
  .btn {
    padding: 4px;
    align-items: center;
    background-color: rgba(15, 1, 1, 0.507);
    color: #fff;
    border-radius: 5px;
    margin-left: 400px;
  }
  
  .imgDisplay {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
  
  .text {
    margin-right: 240px;
  }
  
  .publicrepobtn {
    font-weight: bolder;
    background-color: crimson;
    text-decoration: none;
    border: none;
    color: white;
